
body {
  font-size: 12px;
  font-family: "Microsoft YaHei",Arial,Helvetica,sans-serif;
  color: #222;
  margin: 0;
  padding: 0;
}
.container .title {
  font-size: 10em;
  text-align: center;
  font-family: "Microsoft JhengHei", sans-serif;
  padding-top: 4%;
  padding-bottom: 4%;
}
.title ul {
  padding: 0;
}
.title ul>li {
  display: inline-block;
}
.title ul>li:hover {
  cursor: pointer;
  color: white;
}
.container .signature {
  position: absolute;
  bottom: 0;
}



